export default {
	props:["cid"],
	data() {
		return {
			info: []
		}
	},
	methods: {
		getInfo(){
			uni.request({
				url:"https://api.bailiban.com/api/admin/miniprogram/careerplanning/info/"+this.cid,
				header:{
					"Authorization":uni.getStorageSync("blbUI-token")
				},
				method:"GET",
				dataType:"json",
				success:(res)=> {
					console.log(res)
					if(res.data.status == 200){
						this.info = res.data.data
					}else{
						uni.showToast({
							title:'请求数据失败'
						})
					}
				},
				fail() {
					uni.showToast({
						title:'请求接口失败'
					})
				}
			});
		},
		jionProject(){
			uni.request({
				url:"https://api.bailiban.com/api/admin/miniprogram/my/jionCareerplanning/"+this.cid,
				header:{
					"Authorization":uni.getStorageSync("blbUI-token")
				},
				method:"POST",
				dataType:"json",
				success:(res)=> {
					console.log(res)
					if(res.data.status == 200){
						uni.showModal({
							content: "加入成功",
							showCancel: false,
							success: function (res) {
								if (res.confirm) {
									console.log('用户点击确认');
								} else if (res.cancel) {
									console.log('用户点击取消');
								}
							}
						})
					}else{
						uni.showModal({
							content: res.data.message,
							showCancel: false,
							success: function (res) {
								if (res.confirm) {
									console.log('用户点击确认');
								} else if (res.cancel) {
									console.log('用户点击取消');
								}
							}
						})
					}
				},
				fail() {
					uni.showToast({
						title:'请求接口失败'
					})
				}
			});
		}
	},
	//组件调用后
	created(){
		this.getInfo()
	}
}
